<template>
	<view class="box">
		<view class="order-top">
			<view class="title">待付款</view>
			<!-- <view class="time">请在 00:29:29 内支付，否则将自动取消</view> -->
		</view>
		<view class="middle">
			<view class="title">一键劳作服务</view>
			<view class="">
				<view class="name-money" v-for="(item,index) in order.ProductList">
					<view class="">{{item.ProductName}}</view>
					<view class="">{{item.UnitPrice}}</view>
				</view>
			</view>
			<view class="total">
				<text class="totle-name">合计</text>
				<text class="total-money">￥{{order.Price}}</text>
			</view>
		</view>
		<view class="order-message">
			<view class="title">订单信息</view>
			<view class="order-number">
				<text>订单编号：{{order.OrderNo}}</text>
				<text @click="handleCopy">复制</text>
			</view>
			<view class="creattime">创建时间：{{order.CreateTime}}</view>
		</view>
		<view class="bottom">
			<view class="cancel-order" @click="cancelOrder">取消订单</view>
			<view class="pay-now" @click="payNow">立即付款</view>
		</view>
	</view>
</template>

<script>
	import {
		GetOrderPayAjax
	} from '@/apis/api/modules/buyland'
	export default {
		data() {
			return {
				order: ''
			}
		},
		onLoad() {
			let workList = uni.getStorageSync("workList");
			let items = JSON.parse(workList);
			this.order = items
		},
		methods: {
			/**
			 * 复制订单编号
			 */
			handleCopy() {
				uni.setClipboardData({
					data: this.order.OrderNo,
					success: () => {
						uni.showToast({
							icon: 'none',
							title: '复制成功'
						})
					}
				});
			},
			/**
			 * 取消订单
			 */
			async cancelOrder() {
				const result = await GetClosePackageOrderAjax({
					OrderID: this.order.OrderID,
					token: true
				})
				uni.showToast({
					title: result.Result,
					icon: 'none'
				})
				setTimeout(() => {
					uni.navigateBack({
						delta: 1
					})
				}, 800)
			},
			/**
			 * 支付
			 */
			async payNow() {
				const payOrder = await GetOrderPayAjax({
					orderNo: this.order.OrderNo,
					payType: 2,
					token: true
				})
				let that = this
				uni.requestPayment({
					"provider": 'wxpay',
					"signType": "RSA",
					"appId": payOrder.Result.appId,
					"nonceStr": payOrder.Result.nonceStr,
					"package": payOrder.Result.package,
					"partnerId": payOrder.Result.partnerId,
					"prepayId": payOrder.Result.prepayId,
					"timeStamp": payOrder.Result.timeStamp,
					"paySign": payOrder.Result.paySign,
					success: function(res) {
						uni.showToast({
							title: '支付成功！',
							icon: 'success'
						})
						setTimeout(() => {
							uni.navigateBack({
								delta: 1
							})
						}, 800)
					},
					fail: function(err) {
						uni.showToast({
							title: '支付失败！',
							icon: 'error'
						})
					}
				})
			}
		}
	}
</script>

<style scoped lang="scss">
	.box {
		min-height: 100%;
		background: #F7F7F7;
	}

	.order-top {
		padding: 30rpx;
		background: #00D08F;

		.title {
			font-size: 32rpx;
			font-weight: 500;
			color: #FFFFFF;
			line-height: 44rpx;
			padding-bottom: 10rpx;
		}

		.time {
			font-size: 24rpx;
			font-weight: 400;
			color: #FFFFFF;
			line-height: 34rpx;
		}
	}

	.middle {
		border-bottom: 2rpx solid #F7F9FC;
		padding: 10rpx 30rpx;
		background: #fff;

		.title {
			font-size: 32rpx;
			font-weight: 500;
			color: #333333;
			line-height: 44rpx;
			padding: 20rpx 0;
		}

		.name-money {
			display: flex;
			justify-content: space-between;
			font-size: 28rpx;
			font-weight: 400;
			color: #999999;
			line-height: 40rpx;
			padding-bottom: 20rpx;
		}

		.total {
			display: flex;
			justify-content: space-between;
			padding-bottom: 20rpx;

			.total-name {
				font-size: 28rpx;
				font-weight: 500;
				color: #333333;
				line-height: 40rpx;
			}

			.total-money {
				font-size: 28rpx;
				font-weight: 500;
				color: #FEB042;
				line-height: 40rpx;
			}
		}
	}

	.order-message {
		margin-top: 30rpx;
		padding: 30rpx;
		background: #fff;

		.title {
			font-size: 28rpx;
			font-weight: 500;
			color: #333333;
			line-height: 40rpx;
			padding-bottom: 20rpx;
		}

		.order-number,
		.creattime {
			font-size: 24rpx;
			font-weight: 400;
			color: #666666;
			line-height: 34rpx;
		}

		.order-number {
			display: flex;
			justify-content: space-between;
			padding-bottom: 20rpx;

			text:nth-of-type(2) {
				color: #00D08F;
			}
		}
	}

	.bottom {
		width: 100%;
		box-sizing: border-box;
		position: absolute;
		bottom: 0;
		background: #fff;
		padding: 30rpx;
		display: flex;
		justify-content: flex-end;
		text-align: center;
		line-height: 58rpx;
		border-radius: 30rpx 30rpx 0rpx 0rpx;

		.cancel-order {
			width: 152rpx;
			height: 58rpx;
			border-radius: 29rpx;
			border: 2rpx solid #999999;
			font-size: 24rpx;
			font-weight: 400;
			color: #999999;
			margin-right: 20rpx;
		}

		.pay-now {
			width: 152rpx;
			height: 58rpx;
			border-radius: 29rpx;
			border: 2rpx solid #00D08F;
			font-size: 24rpx;
			font-weight: 400;
			color: #00D08F;
			line-height: 58rpx;
		}
	}
</style>
